const app = getApp()
export default{
	data(){
		return {
			modalName:null,
			list:[]
		}
	},
	methods:{
		showModal(e){
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(){
			this.modalName = null
		},
		Getstorecoupons(coupontypeid){
			const _this = this
			uni.request({
				url:`${app.globalData.Requestpath}activity/userGetStoreCoupon`,
				method:"POST",
				data:{
					token:this.tokey,
					sid:this.storeid,
					cid:coupontypeid
				},
				success(res){
					console.log(res)
					if(res.data.code==0){
						_this.modalName = null
						app.globalData.showtoastsame("领取成功...")
					}else{
						app.globalData.showtoastsame(res.data.msg)
						_this.modalName = null
					}
				}
			})

			
		}
	},
	created() {
		const _this  = this
		// console.log(_this.tokey,_this.storeid)
		uni.request({
			url:`${app.globalData.Requestpath}activity/getStoreCouponTypeList`,
			method:"POST",
			data:{
				token:_this.tokey,
				sid:_this.storeid,
			},
			success(res) {
				if(res.data.code==0){
					_this.list = res.data.data.list
					_this.list.forEach((item,index)=>{
						item.start_time = item.start_time.trim().split(" ")[0]
						item.stop_time = item.stop_time.trim().split(" ")[0]
					})
				}
			}
		})
	},
	props:["tokey","storeid","msg"]
}
